<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">过程管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">过程分类</span>
      <div class="button-content">
        <Button type="primary" @click="addfun(0)">
          <i class="ivu-icon ivu-icon-md-add"></i>新增
        </Button>
        <Button type="primary" v-authcontrol="{menuCode:'108001001005'}" @click="exportFn">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
          <!-- <div class="button-content">
            <Button type="primary" @click="addfun(0)">
              <i class="ivu-icon ivu-icon-md-add"></i>新增
            </Button>
            <Button type="primary">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button>
          </div>-->
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{pageSize*(pageNum-1)+index+1}}</td>
                    <td>
                      <span class="spanBox" @click="rolefun(item.id)">修改</span>
                      <span
                        class="spanBox"
                        @click="delItem(item.id)"
                        v-if="(item.id*1>15?true:false)"
                      >删除</span>
                    </td>
                    <td>{{item.processType}}</td>
                    <td>{{item.content}}</td>
                    <!-- <td>{{item.parentType}}</td> -->
                    <td>{{item.keywords}}</td>
                    <td>{{item.typeDescribe}}</td>
                    <td>{{item.createTime}}</td>
                    <td>{{item.creator}}</td>
                    <td>{{item.upDateTime}}</td>
                    <td>{{item.updater}}</td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
          <div class="flexCenter pageBox">
            <Page
              :total="total"
              :current.sync="pageNum"
              show-elevator
              show-sizer
              @on-change="pageNumFun"
              @on-page-size-change="pageSizeFun"
            ></Page>
          </div>
        </div>
      </div>
    </div>
    <!-- ===========弹框 -->
    <!-- <Button @click="modal1 = true">Custom header and footer</Button> -->
    <Modal :mask-closable="false" v-model="modal1" width="1000" class="dialog" title="新增">
      <div>
        <Form
          ref="formValidateModel1"
          :model="formValidateModel1"
          :rules="ruleValidateModel1"
          :label-width="100"
        >
          <FormItem label="分类名称" prop="name">
            <Input v-model="formValidateModel1.name" placeholder="请输入分类名称"></Input>
          </FormItem>
          <FormItem label="自定义顺序" prop="val2">
            <Input v-model="formValidateModel1.val2" placeholder="请输入自定义顺序"></Input>
          </FormItem>
          <FormItem label="父分类" prop="val3">
            <Select v-model="formValidateModel1.val3" placeholder="请选择">
              <Option value>未选择</Option>
              <Option :value="item" v-for="(item,index) in optionArr" :key="index">{{item}}</Option>
            </Select>
          </FormItem>
          <FormItem label="关键字" prop="val4">
            <Input v-model="formValidateModel1.val4" placeholder="请输入"></Input>
          </FormItem>
          <FormItem label="分类描述" prop="val5">
            <Input v-model="formValidateModel1.val5" placeholder="请输入"></Input>
          </FormItem>
        </Form>
      </div>
      <div slot="footer" class="flexCenter">
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidateModel1')">保存</Button>
          <Button @click="modal1=false">取消</Button>
        </div>
      </div>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ===========弹框 -->
    <!-- <Button @click="modal1 = true">Custom header and footer</Button> -->
    <Modal :mask-closable="false" v-model="modal2" width="1000" class="dialog" title="修改">
      <div>
        <Form
          ref="formValidateModel2"
          :model="formValidateModel2"
          :rules="ruleValidateModel1"
          :label-width="100"
        >
          <FormItem label="分类名称" prop="name">
            <Input v-model="formValidateModel2.name" placeholder="请输入分类名称" disabled></Input>
          </FormItem>
          <FormItem label="自定义顺序" prop="val2">
            <Input v-model="formValidateModel2.val2" placeholder="请输入自定义顺序"></Input>
          </FormItem>
          <!-- <FormItem label="父分类" prop="val3">
            <Select v-model="formValidateModel2.val3" placeholder="请选择">
              <Option value="">未选择</Option>
              <Option :value="item" v-for="(item,index) in optionArr" :key="index" >{{item}}</Option>
            </Select>
          </FormItem>-->
          <FormItem label="关键字" prop="val4">
            <Input v-model="formValidateModel2.val4" placeholder="请输入"></Input>
          </FormItem>
          <FormItem label="分类描述" prop="val5">
            <Input v-model="formValidateModel2.val5" placeholder="请输入"></Input>
          </FormItem>
        </Form>
      </div>
      <div slot="footer" class="flexCenter">
        <div class="button-content">
          <Button type="primary" @click="handleSubmit2('formValidateModel2')">保存</Button>
          <Button @click="modal2=false">取消</Button>
        </div>
      </div>
    </Modal>
    <!-- ===========弹框 -->
  </div>
</template>
<script>
export default {
  created() {
    let that = this;
    this.axios
      .post("/process/getProcessType", {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          that.optionArr = response.data;
          console.log(that.optionArr);
        }
      })
      .catch(error => {});
    this.tableList(this.pageNum, this.pageSize);
  },
  computed: {},
  watch: {
    modal1(a) {
      if (a == false) {
        this.$refs["formValidateModel1"].resetFields();
      }
    },
    modal2(a) {
      if (a == false) {
        this.$refs["formValidateModel2"].resetFields();
      }
    }
  },
  data() {
    return {
      mode2id: "",
      pageSize: 10,
      total: 0,
      pageNum: 1,
      optionArr: [],
      // ==============弹框二
      modal2: false,
      // ==============弹框二
      // ===============弹框一
      modalDisableFlag: true, //修改与新增弹框内容禁用标识
      formValidateModel1: {
        name: "",
        val2: "",
        val3: "", //角色描述
        val4: "" //说明
      },
      formValidateModel2: {
        name: "",
        val2: "",
        val3: "", //角色描述
        val4: "" //说明
      },
      ruleValidateModel1: {
        name: [
          {
            required: true,
            message: "请输入",
            trigger: "blur"
          },
          { max: 10, message: "输入长度过长", trigger: "blur" }
        ],
        val2: [
          {
            required: true,
            validator: (rule, value, callback) => {
              let regu = /^([1-9]\d*|[0]{1,1})$/;
              if (!value && value != 0) {
                callback("自定义顺序不能为空");
                return;
              }
              if (!regu.exec(value)) {
                callback(new Error("自定义顺序请输入非负整数"));
                return;
              }
              if (value.length > 10) {
                callback("自定义顺序不能超过10个字");
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        val4: [{ max: 40, message: "输入长度过长", trigger: "blur" }],
        val5: [{ max: 120, message: "输入长度过长", trigger: "blur" }]
      },
      modal1: false,
      //   ============弹框一
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "分类名称" },
        { name: "自定义顺序" },
        // { name: "父分类" },
        { name: "关键字" },
        { name: "分类描述" },
        { name: "创建时间" },
        { name: "创建人" },
        { name: "更新时间" },
        { name: "更新人" }
      ],

      tableBody: [
        {
          id: "",
          processType: "", //分类名称
          content: "", //自定义顺序
          parentType: "", //父分类校验
          keywords: "", //关键字
          typeDescribe: "", //分类描述
          createTime: "", //创建时间,
          creator: "", //创建人
          upDateTime: "", //更新时间
          updater: "" //更新人
        },
        {
          id: "12",
          code: "eng_1",
          nameClass: "内控制度",
          customOrder: "1",
          fartherClass: "",
          classDis: "",
          createdTime: "2017-03-12 13:12:21",
          createdPeople: "admin",
          uploadTime: "2017-12-31 12:21:10",
          uploadPeople: "admin"
        }
      ]
    };
  },
  methods: {
    resolvemenu() {
      let params = new FormData();
      params.append("menuCode", 108001001000);
      this.axios
        .post(`/system/menu/getLeftMenuByMenuCode`, params, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(response => {
          if (response.code == "200") {
            this.storage.set("leftMenuBoxBody", response.data.listMenu);
            this.$store.commit("leftMenuBoxBody", {
              leftMenuBoxBody: response.data.listMenu
            });
          }
        });
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn() {
      let that = this;
      let data = {
        pageNum: this.pageNum,
        pageSize: this.pageSize
      };
      data = JSON.stringify(data);
      this.axios
        .post(
          `/process/getProcessTypeExcelList/${this.pageNum}/${this.pageSize}`,
          data,
          {
            headers: { "Content-Type": "application/json" },
            responseType: "blob"
          }
        )
        .then(response => {
          this.download(response, "导出文档.xls");
        })
        .catch(error => {});
    },
    pageNumFun(val1) {
      this.pageNum = val1;
      this.tableList(val1, this.pageSize);
    },
    pageSizeFun(val1) {
      this.pageSize = val1;
      this.tableList(this.pageNum, val1);
    },
    tableList(a, b) {
      let that = this;
      let pageNubVal = a;
      let pageSizeVal = b;
      this.axios
        .post(`/process/getProcessTypeList/${pageNubVal}/${pageSizeVal}`)
        .then(response => {
          if (response.code == "200") {
            that.total = response.data.total;
            that.tableBody = response.data.records;
          }
        })
        .catch(function(error) {});
    },
    delItem(a) {
      this.$Modal.confirm({
        title: "是否删除？",
        okText: "确认",
        cancelText: "取消",
        onOk: () => {
          let that = this;
          let data = {
            id: a
          };
          data = JSON.stringify(data);
          this.axios
            .post("/process/deleteProcessType", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.info("删除成功");
                this.tableList(this.pageNum, this.pageSize);
              }
            })
            .catch(error => {});
        },
        onCancel: () => {}
      });
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          let that = this;
          let data = {
            processType: this.formValidateModel1.name,
            content: this.formValidateModel1.val2,
            parentType: this.formValidateModel1.val3,
            keywords: this.formValidateModel1.val4,
            typeDescribe: this.formValidateModel1.val5
          };
          data = JSON.stringify(data);
          this.axios
            .post("/process/addProcessType", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.info("保存成功");
                this.$refs[name].resetFields();
                this.modal1 = false;
                this.tableList(1, this.pageSize);
                this.resolvemenu();
              }
            })
            .catch(error => {});
        } else {
        }
      });
    },
    handleSubmit2(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          let that = this;
          let data = {
            id: this.mode2id,
            processType: this.formValidateModel2.name,
            content: this.formValidateModel2.val2,
            // parentType:this.formValidateModel2.val3,
            parentType: "",
            keywords: this.formValidateModel2.val4,
            typeDescribe: this.formValidateModel2.val5
          };
          data = JSON.stringify(data);
          this.axios
            .post("/process/updateProcessType", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.info("修改成功");
                this.$refs[name].resetFields();
                this.modal2 = false;
                 this.resolvemenu();
              }
            })
            .catch(error => {});
        } else {
        }
      });
    },
    watchfun() {
      this.$router.push({ name: "dataExtractionApplyDetail" });
    },
    addfun(index) {
      this.modalDisableFlag = false;
      this.modal1 = true;
    },
    modify(index) {
      this.modalDisableFlag = true;

      this.modal1 = true;
    },
    rolefun(a) {
      let that = this;
      let idVal = a;
      this.mode2id = a;
      this.axios
        .post(`/process/getProcessTypeRow/${idVal}`)
        .then(response => {
          if (response.code == "200") {
            this.formValidateModel2.name = response.data.processType;
            this.formValidateModel2.val2 = response.data.content;
            this.formValidateModel2.val3 = response.data.parentType;
            this.formValidateModel2.val4 = response.data.keywords;
            this.formValidateModel2.val5 = response.data.typeDescribe;
          }
        })
        .catch(function(error) {});
      //调用弹框2
      this.modal2 = true;
    }
  }
};
</script>
<style scoped>
.rightTopbtn {
  margin: 15px 4px;
  float: right;
}
.btnClass {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
.operationSpan {
  color: blue;
  margin: 0 5px;
  cursor: pointer;
}
.roleBox {
  padding: 3px 0;
  cursor: pointer;
  color: blue;
}
.roleBox:hover {
  background-color: RGB(241, 243, 244);
}
</style>



